html, :root, *, *::after, *::before {
	box-sizing: border-box;
}

button, a, input, select {
	cursor: pointer;
}

*:disabled {
	cursor: default;
}

/* Preferred Default Font */
@font-face {
  font-family: 'atkinson-hyper-legible';
  src: url('../assets/fonts/atkinsonhyperlegible-regular-webfont.woff2') format('woff2'),
       url('../assets/fonts/atkinsonhyperlegible-regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

:root {
  font-size: 20px;
	font-family: 
		'atkinson-hyper-legible',
		system-ui,
		'Segoe UI',
		Roboto,
		Helvetica,
		Arial,
		sans-serif,
		'Apple Color Emoji',
		'Segoe UI Emoji';
		tab-size: 4;
		line-height: 1.15;
		letter-spacing: .12em; 
		-webkit-text-size-adjust: 100%;
		cursor: default;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
}


